
                @import "./src/Core/styles/index.scss";
        
.container {
    position        : relative;
    width           : 100%;
    color           : $color-light;
    background-color: $color-primary;
    padding         : 30px 0;
    overflow        : hidden;

    >div {
        position: relative;
        @include content(1000px);
        z-index: 10;

        >h1 {
            font-size  : 48px;
            font-weight: 600;
        }

        >p {
            margin-top : 20px;
            font-size  : 20px;
            line-height: 30px;
        }
    }

    >img {
        position: absolute;
        z-index : 0;

        top: 0;

        &:first-of-type {
            left: 0;
        }

        &:last-of-type {
            right    : 0;
            transform: rotateY(180deg);
        }
    }
}